import articlebrief from '../../components/articlebrief';
import mainpagetop from '../../components/mainpagetop.vue';
import mainpageleft from '@/components/mainpageleft.vue'

export default {
	data() {
		return {
			title: 'Hello',
			hottitlelist: ['热门标题111', '热门标题222', '热门标题333'],
			showlist: '',
			topArticles: '',

			userInfo: { // 默认user设置
				id: 'test-id123',
				nickname: 'test-name',
				faceImg: '',
				faceImgThumb: '',
				email: 'zy22089@nottingham.edu.cn',
				emailPrefix: 'zy22089',
				emailSuffix: '@nottingham.edu.cn'
			},
			
		};
	},
	components: {
		articlebrief,
		mainpagetop,
		mainpageleft,
	},

	onLoad() {			
		var userInfo = this.getGlobalUserInfo();
		if (this.isNull(userInfo)) {
			uni.navigateTo({
				url: "../wechatLogin/wechatLogin"
			})
			return;
		}
		
		this.showArticles(); // 显示文章流
		
		this.getTop3Articles(); // 获取热度榜
		
		this.mySocket.init(); // 初始化 Socket, 离线调试请注释掉
		
		// [测试代码块]
	},
	onShow() {
		var userInfo = this.getGlobalUserInfo();
		if (!this.isNull(userInfo)) {
			// 设置 userInfo 传给 mainpagetop 组件
			this.userInfo = this.getGlobalUserInfo();
		}
	},
	methods: {
		showArticles() {
			var _this = this;
			uni.request({
				url: 'http://127.0.0.1:8080/article/queryAllArticles',
				method: "POST",
				success: (res) => {
					_this.showlist = res.data.data.rows;
					// console.log(res)
				},
				fail: (res) => {
					console.log("index unirequest fail");
					console.log(res);
				}
			});
		},
		
		getTop3Articles(){
			var that = this;
			uni.request({
				url: 'http://127.0.0.1:8080/article/getHotTop3',
				method: "POST",
				success: (res) => {
					that.topArticles = res.data.data;
					console.log(res)
				}
			})
		}
	}
};
